<template>
  <div class="volun charityProve">
    <header>
      <div class="proveV" :style="{backgroundImage: 'url('+note+')'}">
        <div class="apply b">1. "" </div>
        <div class="apply b">2.</div>
        <div class="apply b">3.</div>
      </div>
    </header>
    <div class="selectTime">
      <div class="lc-tip">
          <span class="block"></span>
          <span></span>
      </div>
    </div>
    <div class="selectS">
      <div class="row begin" @click="open(0)">
        <span class="col-sm col-sm-6 fTime"></span>
        <span class="col-sm col-sm-6 r">
          <span v-if="!form.beginDay"></span>
          <span v-else>{{form.beginDay}}</span>
          <i class="iconfont icon-arrow-right"></i>
        </span>
      </div>
      <div class="row" @click="open(1)">
        <span class="col-sm col-sm-6 fTime"></span>
        <span class="col-sm col-sm-6 r">
          <span v-if="!form.endDay"></span>
          <span v-else>{{form.endDay}}</span>
          <i class="iconfont icon-arrow-right"></i>
        </span>
      </div>
    </div>
    <section class="button-box">
        <button @click="submit"></button>
    </section>
    <mt-datetime-picker
      ref="picker"
      type="date"
      v-model="value"
      @confirm="startConfirm"
      :endDate="endDate"
      placeholder="">
      <!-- endDate: -->
    </mt-datetime-picker>
    <mt-datetime-picker
      ref="picker2"
      type="date"
      v-model="value2"
      @confirm="endConfirm"
      :startDate="startDate2"
      :endDate="endDate2"
      placeholder="">
    </mt-datetime-picker>
  </div>
</template>

<script>
export default {
    data () {
        return {
            curDate: new Date(),
            parDate: null,
            endDate: null,
            endDate2: null,
            startDate2: null,
            value: null,
            value2: null,
            form: {
                beginDay: null,
                endDay: null,
                user: null,
                beginDay1: null,
                endDay1: null,
                certificateCode: null
            },
            note: require('@/assets/images/charity-1_02.png')
        };
    },
    created () {
        let curDate = new Date();
        this.parDate = new Date(curDate.getTime() - 24 * 60 * 60 * 1000); // 
        this.endDate = this.parDate;
        this.endDate2 = this.parDate;
        this.startDate2 = this.parDate;
        this.value = this.parDate;
        this.value2 = this.parDate;
        this.form.certificateCode = this.$route.query.certificateCode;
        let pfa = window.localStorage.getItem('personFirstAccount');
        pfa && (this.form.user = pfa);
        // this.form.user = '72-00001666-5';
    },
    methods: {
        open (v) {
            v === 0 ? this.$refs.picker.open() : this.$refs.picker2.open();
        },
        startConfirm (v) {
            this.form.beginDay = this.date(v);
            this.form.beginDay1 = this.date(v, 'n');
            this.startDate2 = v;
            this.value2 = v;
        },
        endConfirm (v) {
            this.form.endDay = this.date(v);
            this.form.endDay1 = this.date(v, 'n');
        },
        submit () {
            if (this.form.beginDay === null) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.endDay === null) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (Number(this.form.beginDay1.substr(0, 4)) !== Number(this.form.endDay1.substr(0, 4))) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            this.$router.push({name: 'duration', query: this.form});
        },
        date (time, n) {
            let y, m, d, date, data;
            date = new Date(time);
            y = date.getFullYear();
            m = date.getMonth() + 1;
            d = date.getDate();
            if (n) {
                data = y + '' + format(m) + '' + format(d) + '';
                return data;
            }
            data = y + '-' + format(m) + '-' + format(d);
            return data;
            function format (num) {
                if (parseInt(num) < 10) {
                    num = '0' + num;
                }
                return num;
            }
        }
    }
};
</script>



