<template>
  <div class="prove-detailedList prove-duration">
      <header>
          <div class="t">
              <div class="title period">/Service Period</div>
              <div>{{query.beginDay1}}  {{query.endDay1}}</div>
          </div>
          <div class="b">
              <div class="title time">/Service Time</div>
              <div>{{form.realTimes}}</div>
          </div>
          <div class="m-b">
              <div class=""></div>
          </div>
          <img src="../assets/images/detailed.png" alt="">
      </header>
      <ul>
          <li class="row" v-for="(item, index) in form.userTicketValidSignRespList" :key="index" @click="open(item)">
              <div class="col-sm col-sm-4 l">
                <div>{{item.startTime | date}}</div>
              </div>
              <div class="col-sm col-sm-8 r">
                <div class="t">
                  <span>{{item.times | numToCash(2)}}</span>
                </div>
                <div class="c">
                  <i class="iconfont icon-person-blod"></i>
                  <span>{{item.name}}</span>
                </div>
                <div class="b">
                  <i class="iconfont icon-address-blod"></i>
                  <span>{{item.orgName}}</span>
                </div>
              </div>
          </li>
        </ul>
      <div v-if="model1">
        <div class="mask"></div>
        <div class="timePublic model">
            <div class="t row" :style="{backgroundImage: 'url('+note+')'}">
                <div class="close" @click="model1 = false">
                <i class="iconfont icon-close"></i>
                </div>
                <div class="text">
                <div class="name"></div>
                <div class="number">{{openForm.recruitCode}}</div>
                </div>
            </div>
            <div class="b">
                <div> : {{openForm.startTime | date }}</div>
                <div>{{1000 | numToCash(2) }}</div>
                <div> : {{openForm.arriveAddress}}</div>
                <!-- <div> : {{openForm.demo}}</div> -->
            </div>
        </div>
      </div>
      <div v-if="model2">
        <div class="mask"></div>
        <div class="model model-1 model-new">
          <div class="t">
            <img src="../assets/images/accomplish-1.png" alt="">
          </div>
          <div class="c"></div>
          <div class="b">7</div>
          <div class="btn-wrap-1 row">
            <button class="col-sm col-sm-4 l" @click="model2 = false"></button>
            <button class="col-sm col-sm-5 r" @click="lookGS"></button>
          </div>
        </div>
      </div>
      <section class="button-box">
          <button @click="submit"></button>
      </section>
  </div>
</template>

<script>
export default {
    data () {
        return {
            note: require('@/assets/images/prove-head.png'),
            isRealTimes: null,
            allLoaded: false,
            bottomText: '...',
            model1: false,
            model2: false,
            form: {},
            query: {},
            openForm: {}
        };
    },
    created () {
        this.query = this.$route.query;
        this.getList();
    },
    methods: {
        getList () {
            let params = {
                user: this.query.user,
                beginDay: this.query.beginDay,
                endDay: this.query.endDay
            };
            this.$http.post(this.$api.prove.queryServiceRecord, params).then((res) => {
                if (res.data.code === 0) {
                    this.form = res.data.data;
                    this.query['time'] = this.form.realTimes;
                    this.form.realTimes = this.initTime(this.form.realTimes);
                    this.isRealTimes = false;
                }
            });
        },
        open (item) {
            this.model1 = true;
            this.openForm = item;
        },
        submit () {
            let params = {
                personFirstAccount: this.query.user,
                certificateCode: this.query.certificateCode,
                startDay: this.query.beginDay,
                endDay: this.query.endDay,
                time: this.query.time
            };
            this.$http.post(this.$api.prove.applyCertificate, params).then((res) => {
                if (res.data.code === 0) {
                    this.model2 = true;
                    return;
                }
                this.$mint.Toast({
                    message: res.data.msg,
                    position: 'top',
                    duration: 1000
                });
            });
        },
        lookGS () {
            this.$router.push({name: 'publicity'});
        },
        initTime (v) {
            if (v < 60) {
                return v + '';
            }
            return Math.floor(v / 60) + '' + (v % 60 === 0 ? '' : v % 60 + '');
        }
    }
};
</script>

<style lang="scss" scoped>
  .prove-duration {
    .model-new {
      .b, .btn-wrap-1 {
        padding: 0 px2rem(20);
      }
      .btn-wrap-1 {
        button {
          border-radius: 3px;
        }
      }
    }
  }
</style>



