<template>
  <div class="prove-idealRecord prove-verify">
      <div class="body" :style="{backgroundImage: 'url('+note+')'}">
      <header>
          <div class="zh">     </div>
          <div class="en">CERTIFICATE OF VOLUNTARY SERVICE</div>
          <div class="number">(No.)AABBCCDDDEEEEEEEEEYYYYMMDD</div>
        </header>
        <div class="line"></div>
        <section class="base">
          <div class="infoTip mb-10">Information of charitarian</div>
          <div class="head">
            <div class="t">/Name</div>
            <div class="b mb-10">,,</div>
            <div class="t">/C&V Passport No.</div>
            <div class="b mb-10">AABBCCDDDEEEEEEEEE</div>
            <div class="t">/Type of ID</div>
            <div class="b mb-10"></div>
            <div class="t">/ID No.</div>
            <div class="b">310291029381728394</div>
          </div>
          <div class="line"></div>
          <div class="t"></div>
          <div class="b">10</div>
          <div class="line"></div>
          <div class="t">Charity Service Content</div>
          <div class="button"></div>
          <div class="b"></div>
          <div class="line"></div>
          <div class="t">Other Information</div>
          <div class="n"></div>
          <div class="line"></div>
          <div class="t">With Attachment or Not?</div>
          <div class="b mb-10 select row">
              <div class="l col-sm col-sm-6">
                <div @click="isClick=true">
                  <span class="radio">
                    <i class="iconfont icon-tick" v-if="isClick"></i>
                  </span>
                  <span class="radio-text">/Yes</span>
                </div>
              </div>
              <div class="r col-sm col-sm-6">
                <div @click="isClick=false">
                  <span class="radio">
                    <i class="iconfont icon-tick" v-if="!isClick"></i>
                  </span>
                  <span class="radio-text">/No</span>
                </div>
              </div>
          </div>
          <div class="t">(Signed by)</div>
          <div class="b"></div>
          <div class="b time">20170619</div>
        </section>
    </div>
    <div class="tip">:http://www.shcvs.cn</div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            isClick: true,
            note: require('@/assets/images/idealRecord-bg.png')
        };
    },
    created () {
    },
    methods: {
    }
};
</script>



