<template>
  <div class="prove-idealRecord">
      <div class="body" :style="{backgroundImage: 'url('+note+')'}">
        <header>
            <div class="zh">{{form.title.split("").join(" ")}}</div>
            <div class="en">CERTIFICATE OF VOLUNTARY SERVICE</div>
            <div class="number">(No.){{form.code}}</div>
          </header>
          <div class="line"></div>
          <section class="base">
            <div class="infoTip mb-10">Information of charitarian</div>
            <div class="head">
              <div class="t">/Name</div>
              <div class="b mb-10">{{form.realName}}</div>
              <div class="t">/C&V Passport No.</div>
              <div class="b mb-10">{{form.code.substring(0, 18)}}</div>
              <div class="t">/Type of ID</div>
              <div class="b mb-10">{{form.cartType}}</div>
              <div class="t">/ID No.</div>
              <div class="b">{{form.cartNO}}</div>
            </div>
            <div class="line"></div>
            <div class="t">/Charity/Donation Times</div>
            <div class="b">{{initTime(form.time)}}</div>
            <div class="line"></div>
            <div class="t">Charity Service Content</div>
            <div class="button" @click="look(form)"></div>
            <div class="b"></div>
            <div class="line"></div>
            <div class="t">Other Information</div>
            <div class="n">{{form.info || ''}}</div>
            <div class="line"></div>
            <div class="t">With Attachment or Not?</div>
            <div class="b mb-10 select row">
                <div class="l col-sm col-sm-6">
                  <div>
                    <span class="radio">
                      <i class="iconfont icon-tick" v-if="form.isAnnex == 1"></i>
                    </span>
                    <span class="radio-text">/Yes</span>
                  </div>
                </div>
                <div class="r col-sm col-sm-6">
                  <div>
                    <span class="radio">
                      <i class="iconfont icon-tick" v-if="form.isAnnex == 0"></i>
                    </span>
                    <span class="radio-text">/No</span>
                  </div>
                </div>
            </div>
            <div class="t">(Signed by)</div>
            <div class="b">{{form.issued}}</div>
            <div class="b time">{{form.createTime | initDate}}</div>
            <div class="tip">: <a :href="form.urlIssued">{{form.demo}}</a></div>
          </section>
      </div>
      <div class="button-w">
        <button><a :href="form.downloadUrl" :download="form.title + '.pdf'">PDF</a></button>
      </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            note: require('@/assets/images/idealRecord-bg.png'),
            form: {}
        };
    },
    created () {
        this.form = this.$route.query;
        console.log(this.form);
    },
    methods: {
        look (item) {
            let params = {
                startDay: this.form.startDay,
                endDay: this.form.endDay,
                code: this.form.code,
                time: this.form.time
            };
            this.$router.push({name: 'idealList', query: params});
        },
        initTime (v) {
            if (v < 60) {
                return v + '';
            }
            return Math.floor(v / 60) + '' + (v % 60 === 0 ? '' : v % 60 + '');
        },
        downloadOpen (item) {
            this.$router.push({name: 'volunteerService', query: item});
        }
    }
};
</script>



