<template>
  <div class="charityProve">
    <header>
      <div class="prove" :style="{backgroundImage: 'url('+note+')'}">
        <div class="apply stay">1</div>
        <div class="apply cannot">2</div>
      </div>
    </header>
    <div class="selectTime">
      <div class="lc-tip">
          <span class="block"></span>
          <span></span>
      </div>
    </div>
    <div class="selectS">
      <div class="row begin" @click="open(0)">
        <span class="col-sm col-sm-6 fTime"></span>
        <span class="col-sm col-sm-6 r">
          <span v-if="!form.beginDay"></span>
          <span v-else>{{form.beginDay}}</span>
          <i class="iconfont icon-arrow-right"></i>
        </span>
      </div>
      <div class="row" @click="open(1)">
        <span class="col-sm col-sm-6 fTime"></span>
        <span class="col-sm col-sm-6 r">
          <span v-if="!form.endDay"></span>
          <span v-else>{{form.endDay}}</span>
          <i class="iconfont icon-arrow-right"></i>
        </span>
      </div>
    </div>
    <button class="submit-btn-1" @click="submit"></button>
    <mt-datetime-picker
      ref="picker"
      type="date"
      @confirm="startConfirm"
      placeholder="">
    </mt-datetime-picker>
    <mt-datetime-picker
      ref="picker2"
      type="date"
      @confirm="endConfirm">
    </mt-datetime-picker>
  </div>
</template>

<script>
export default {
    data () {
        return {
            form: {
                beginDay: null,
                endDay: null,
                user: null,
                beginDay1: null,
                endDay1: null,
                certificateCode: null
            },
            note: require('@/assets/images/charity-1_02.png')
        };
    },
    created () {
        this.form.certificateCode = this.$route.query.certificateCode;
        let pfa = window.localStorage.getItem('personFirstAccount');
        pfa && (this.form.user = pfa);
    },
    methods: {
        open (v) {
            v === 0 ? this.$refs.picker.open() : this.$refs.picker2.open();
        },
        startConfirm (v) {
            this.form.beginDay = this.date(v);
            this.form.beginDay1 = this.date(v, 'n');
        },
        endConfirm (v) {
            this.form.endDay = this.date(v);
            this.form.endDay1 = this.date(v, 'n');
        },
        submit () {
            if (this.form.beginDay === null) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.endDay === null) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            this.$router.push({name: 'proveTime', query: this.form});
        },
        date (time, n) {
            let y, m, d, date, data;
            date = new Date(time);
            y = date.getFullYear();
            m = date.getMonth() + 1;
            d = date.getDate();
            if (n) {
                data = y + '' + format(m) + '' + format(d) + '';
                return data;
            }
            data = y + '-' + format(m) + '-' + format(d);
            return data;
            function format (num) {
                if (parseInt(num) < 10) {
                    num = '0' + num;
                }
                return num;
            }
        }
    }
};
</script>



