<template>
  <div class="prove-reportLog prove-report">
      <section class="lc-tips">
          <i class="iconfont icon-jay"></i>
      </section>
      <section class="form">
        <div class="item row">
            <div class="col-sm col-sm-4 l"></div>
            <input class="col-sm col-sm-8 r" type="text" v-model="form.reportObj" placeholder="">
        </div>
        <textarea cols="30" rows="10" v-model="form.content" placeholder=""></textarea>
        <div class="item row">
            <div class="col-sm col-sm-4 l"></div>
            <input class="col-sm col-sm-8 r" type="text" v-model="form.reportName" placeholder="">
        </div>
        <div class="item row">
            <div class="col-sm col-sm-4 l"></div>
            <input class="col-sm col-sm-8 r" type="text" v-model="form.reportCardNo" placeholder="">
        </div>
        <div class="item row">
            <div class="col-sm col-sm-4 l"></div>
            <input class="col-sm col-sm-8 r" type="tel" maxlength="11" v-model="form.reportMobile" placeholder="">
        </div>
        <div class="item row">
            <div class="col-sm col-sm-4 l"></div>
            <input class="col-sm col-sm-4 r" v-model="form.key" maxlength="6" type="tel" placeholder="">
            <div class="col-sm col-sm-4 codebtn" :class="{'isCode': !flag}"  @click="sendCode">{{verCode}}</div>
        </div>
      </section>
      <div class="button-wrap">
        <button class="submit-btn-1" @click="submit"> </button>
      </div>
      <div v-if="isTips">
        <div class="mask"></div>
        <div class="model">
          <div class="t">
            <div class="close">
              <i class="iconfont icon-close"></i>
            </div>
            <i class="iconfont icon-jay"></i>
            <span></span>
          </div>
          <div class="c">
              
          </div>
          <div class="b btnWrap">
              <div class="l">
                  <button @click="jump"> </button>
              </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            flag: true,
            number: 60,
            verCode: '',
            code: null,
            form: {
                code: null,
                reportObj: '',
                reportName: null,
                reportMobile: null,
                content: null,
                list: [],
                key: null
            },
            isTips: false
        };
    },
    created () {
        this.form.code = this.$route.query.code;
        this.form.list = this.$route.query.list;
    },
    methods: {
        sendCode () {
            if (!(/^(0|86|17951)?(13[0-9]|15[0-9]|166|17[3678]|18[0-9]|19[0-9]|14[57])[0-9]{8}$/.test(this.form.reportMobile))) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            const num = 59;
            if (this.flag) {
                let params = {
                    mobile: this.form.reportMobile,
                    signName: ''
                };
                this.$http.post(this.$api.prove.sendSms, params).then((res) => {
                    if (res.data.code === 0) {
                        this.number = num;
                        var timing = setInterval(() => {
                            this.verCode = this.number + 's';
                            this.number--;
                            if (this.number < 0) {
                                window.clearInterval(timing);
                                this.verCode = '';
                                this.flag = true;
                            }
                        }, 1000);
                    } else {
                        this.flag = true;
                        this.$mint.Toast({
                            message: res.data.msg,
                            position: 'top',
                            duration: 1000
                        });
                    }
                });
            }
        },
        submit () {
            if (this.form.reportObj === null) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.content === null) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.reportName === null) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.reportCardNo === null) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (!(/^(0|86|17951)?(13[0-9]|15[0-9]|166|17[3678]|18[0-9]|19[0-9]|14[57])[0-9]{8}$/.test(this.form.reportMobile))) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.key === null) {
                this.$mint.Toast({
                    message: '',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            this.form.code = this.$route.query.code;
            this.form.list = this.$route.query.list;
            this.$http.post(this.$api.prove.report, this.form).then((res) => {
                if (res.data.code === 0) {
                    this.isTips = true;
                }
            });
        },
        jump () {
            this.isTips = false;
            this.$router.push({name: 'publicity'});
        }
    }
};
</script>
<style lang="scss" scoped>
  .prove-report {
    .form textarea {
      width: 91.5%;
      margin-top: 5px;;
    }
    .isCode {
      background-color: #616161!important;
    }
    .btnWrap {
      .l {
        text-align: center;
      }
    }
  }
</style>



